import { createContext, useContext } from 'react';

const reducer = (state, { type, data }) => {
    switch (type) {
        case 'set_error':
            return Object.assign(Object.assign({}, state), { errored: data.error
                    ? state.errored.concat(data.id)
                    : state.errored.filter((id) => id !== data.id) });
        case 'register_component':
            return Object.assign(Object.assign({}, state), { registered: data.unregister
                    ? state.registered.filter((id) => id !== data.id)
                    : state.registered.concat(data.id) });
    }
};
const FieldsetContext = createContext({
    isFieldset: false,
    dispatch: () => { },
});
FieldsetContext.displayName = 'FieldsetContext';
const useFieldsetContext = () => useContext(FieldsetContext);
const FieldsetProvider = FieldsetContext.Provider;

export { FieldsetProvider, reducer, useFieldsetContext };
