import { createContext, useContext } from 'react';

const TableContext = createContext({
    responsiveHeadings: [],
    registerHeadings: () => { },
});
const TableHeadContext = createContext({
    head: false,
});
const useTableContext = () => useContext(TableContext);
const TableProvider = TableContext.Provider;
const useTableHeadContext = () => useContext(TableHeadContext);
const TableHeadProvider = TableHeadContext.Provider;

export { TableHeadProvider, TableProvider, useTableContext, useTableHeadContext };
